import React from 'react';
import { NavLink } from 'react-router-dom';
import { icoHome, icoAbout } from '../ico/ico.js';

class MenuPage extends React.Component {
    render() {
        return (
            <React.Fragment>
                <li><NavLink to="/" exact className="PageLink" activeClassName="ActivePageLink"> {icoHome()}  </NavLink></li>
                <li> <NavLink to="/about" className="PageLink" activeClassName="ActivePageLink"> {icoAbout()}  </NavLink></li>
            </React.Fragment>
        );
    }
}

export default MenuPage;




